<template>
  <!-- submit.prevent="false" 阻止默认提交事件 -->
  <form :class="inline ? 'form-inline' : '' " class="zoom-form">
    <slot></slot>
  </form>
</template>
<script>
export default {
  name: "zoom-form",
  props: {
    inline: {
      type: [String, Boolean],
      default: null
    },
    labelWidth: {
      type: [String, Boolean],
      default: null
    }
  },
  methods: {
    /**
     * 验证功能 循环遍历form表单,查看表单里组件是否有handleBlur验证函数 如果有就调用
     */
    valid() {
      let arr = this.$children;
      let count = 0;
      arr.forEach(item => {
        let componentInstance = item.$slots.content[0].componentInstance;
        if (
          componentInstance &&
          componentInstance.handleBlur
        ) {
          let res = componentInstance.handleBlur();
          /**
           * 用户是否添加了必填项(require)
           * 如果添加了require, 并且表单项的值为空, 则弹出提示 '必填'
           * 2s 后消失
           */
          if (item.require && !componentInstance.currentValue) {
            let msg = componentInstance.errMsg;
            componentInstance.errMsg = '必填';
            componentInstance.error = true;
            res = false;
            setTimeout(() => {
              componentInstance.errMsg = msg;
              componentInstance.error = false;
            }, 2000);
          }
          // 如果有一项验证未通过, count + 1
          // 最终结果取反 只有res为 0 才返回 true
          if (!res) count += 1;
        }
      });
      return !count;
    },
    // 重置表单内容
    /**
     * form表单 利用zoom表单组件中的reset函数, 重置组件数据
     */
    reset() {
      let arr = this.$children;
      arr.forEach(item => {
        if (
          item.$slots.content[0].componentInstance &&
          item.$slots.content[0].componentInstance.reset
        ) {
          item.$slots.content[0].componentInstance.reset();
        }
      });
    }
  }
};
</script>
<style>
.zoom-form {
  width: 100%;
  height: 100%;
  overflow: hidden;
}
</style>